<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="组织名称" size="mini">
            <erpCompanyList v-model='params.company_id' :fatherParams='params' />
          </el-form-item>
          <el-form-item label="厂家名称" size="mini">
            <erpSupplier v-model='params.supplier_id' @change="onSearch(1)" />
          </el-form-item>
          <el-form-item label="返利政策名称" size="mini">
            <el-input v-model="params.rebate_policy_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="返利模式" size="mini">
            <el-select clearable v-model="params.rebate_mode" placeholder="请选择" @change="onSearch(1)">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="满量返点" :value="1"></el-option>
              <el-option label="短期促销" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="返利有效期" size="mini">
            <el-date-picker v-model="policyDay" type="daterange" value-format='yyyy-MM-dd' range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="返利周期" size="mini">
            <cycleBox v-model="params.rebate_cycle" @change="onSearch(1)" />
          </el-form-item>

          <el-form-item label="阶梯模式" size="mini">
            <el-select clearable v-model="params.ladder_mode" placeholder="请选择" @change="onSearch(1)">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="无阶梯模式" :value="1"></el-option>
              <el-option label="有阶梯模式" :value="2"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择" @change="onSearch(1)">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="未审核" :value="1"></el-option>
              <el-option label="已审核" :value="2"></el-option>
              <el-option label="启用" :value="4"></el-option>
              <el-option label="暂停" :value="5"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="创建日期" size="mini">
            <el-date-picker v-model="createdDay" type="daterange" value-format='yyyy-MM-dd' range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="创建人" size="mini">
            <el-input v-model="params.add_user_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="examineDay" type="daterange" value-format='yyyy-MM-dd' range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"> </el-date-picker>
          </el-form-item>
          <el-form-item label="审核人" size="mini">
            <el-input v-model="params.auditor_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="goAdd({})" class="addBtn" size="mini">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="examination('删除')" size="mini" v-if="authority.b_del == 1">删除</el-button>
        <el-button type="primary" icon="iconfont icon-pass" @click="examination('审核')" size="mini" v-if="authority.b_pass == 1">审核</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="examination('弃审')" class="addBtn" size="mini" v-if="authority.b_nopass == 1">弃审</el-button>
        <el-button type="primary" icon="iconfont icon-pass" @click="examination('启用')" size="mini" v-if="authority.b_open == 1">启用</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="examination('暂停')" class="addBtn" size="mini" v-if="authority.b_pause == 1">暂停</el-button>
        <el-dropdown v-if="authority.b_export == 1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div>
        <xTable :ajax='{ url: "getRebatePolicyListPurchase", params: params }' ref='xTable' :tableColumn='tableColumn' />
      </div>
    </div>
  </div>
</template>

<script>
import { paramsModule, tableModule } from "./config/factoryPolicy";
import cycleBox from "./config/cycleBox";
export default {
  mixins: [tableModule],
  name: "shoplowPriceList",
  data() {
    return {
      policyDay: [], // 策略年月
      createdDay: [], // 创建日期
      examineDay: [], // 审核日期
      params: this.$publicFution.deepCopy(paramsModule)
    };
  },
  methods: {
    onReset() {
      this.policyDay = []; // 策略年月
      this.createdDay = []; // 创建日期
      this.examineDay = []; // 审核日期
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.onSearch(1);
    },
    shopChange(val) {
      this.params.goods_type_id = val[2];
    },
    examination(type) {
      if (this.multipleSelection.length === 0) {
        this.$message.warning("请勾选单据！");
        return;
      }
      var box = this.multipleSelection.map(val => val.rebate_policy_id);
      var api;
      if (type === "审核") {
        api = "auditorRebatePolicyPurchase";
      } else if (type === "弃审") {
        api = "unAuditorRebatePolicyPurchase";
      } else if (type === "启用") {
        api = "openRebatePolicyPurchase";
      } else if (type === "暂停") {
        api = "pauseRebatePolicyPurchase";
      } else if (type === "删除") {
        api = "delRebatePolicyPurchase";
      }
      this.$confirm("是否继续?", "温馨提示！", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$axios({
            method: "post",
            data: { rebate_policy_id: box.join(",") },
            url: this.$api[api]
          }).then(({ data }) => {
            if (data.code === 200) {
              this.onSearch();
              this.$message.success(data.msg);
            }
          });
        })
        .catch(() => {});
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axios({
        method: "get",
        url: this.$api.exportRebatePolicyUrl,
        params: s
      }).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    getTime(arr) {
      if (Array.isArray(arr)) {
        return arr;
      }
      return [];
    },
    onSearch(number) {
      this.params.effective_start_time = this.getTime(this.policyDay)[0];
      this.params.effective_end_time = this.getTime(this.policyDay)[1];
      this.params.add_start_time = this.getTime(this.createdDay)[0];
      this.params.add_end_time = this.getTime(this.createdDay)[1];
      this.params.auditor_start_time = this.getTime(this.examineDay)[0];
      this.params.auditor_end_time = this.getTime(this.examineDay)[1];
      this.$refs.xTable._getData(number);
    },
    goDetail(row) {
      this.$router.push({
        path: "/purchase/factoryPolicy/factoryPolicySee",
        query: {
          id: row.rebate_policy_id
        }
      });
    },
    goAdd(row) {
      this.$router.push({
        path: "/purchase/factoryPolicy/factoryPolicyAdd",
        query: {
          id: row.rebate_policy_id
        }
      });
    }
  },
  components: {
    cycleBox
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>

