export const paramsModule = {
  company_id: localStorage.getItem('company_id'),
  company_name: localStorage.getItem('company_name'),
  rebate_policy_name: '', // 返利政策名称
  rebate_mode: 0, // 返利模式
  effective_start_time: '',// 返利有效期开始时间
  effective_end_time: '',// 返利有效期结束时间
  rebate_cycle: 0,// 返利周期
  ladder_mode: 0,// 阶梯模式
  auditor_status: 0, // 审核状态
  add_start_time: '',// 新增开始时间
  add_end_time: '',// 
  add_user_name: '',// 新增人
  auditor_start_time: '',// 审核时间
  auditor_end_time: '',//
  auditor_name: '',
}

export const tableModule = {
  data() {
    return {
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center',
          fixed: 'left'
        },
        {
          field: 'name',
          title: '操作',
          width: 100,
          fixed: "right",
          slots: {
            default: (scope) => {
              return [
                <el-button size="small" type="text" disabled={this.authority.b_info !== 1} onClick={() => this.goDetail(scope.row)} > 查看 </el-button>,
                <el-button size="small" type="text" disabled={!((scope.row.auditor_status === 1) && this.authority.b_edit === 1)} onClick={() => this.goAdd(scope.row)}  > 修改  </el-button>
              ]
            }
          }
        },
        {
          title: '单据号',
          field: 'rebate_policy_sn',
          width: 170,
          slots: {
            default: (scope) => {
              return [
                <a onClick={() => { this.goDetail(scope.row) }} type="text" size="small">
                  {scope.row.rebate_policy_sn} </a>
              ]
            }
          }
        },
        {
          title: '组织名称',
          field: 'company_name',
          width: 160
        },
        {
          title: '厂家名称',
          field: 'supplier_name',
          width: 150
        },
        {
          title: '返利政策名称',
          field: 'rebate_policy_name',
          width: 120
        },
        {
          title: '返利模式',
          field: 'rebate_mode_name',
          width: 80
        },
        {
          title: '返利有效期',
          field: 'effective_time',
          width: 180
        },
        {
          title: '返利周期',
          field: 'rebate_cycle_name',
          width: 80,
          formatter: ({row}) => {
            if (row.rebate_mode === 2) {
              return ''
            }
            return row.rebate_cycle_name
          },
        },
        {
          title: '阶梯模式',
          field: 'ladder_mode_name',
          formatter: ({row}) => {
            if (row.rebate_mode === 2) {
              return ''
            }
            return row.ladder_mode_name
          },
          width: 100
        },
        {
          title: '状态',
          field: 'auditor_status_name',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 80
        },
        {
          title: '创建时间',
          field: 'add_time',
          width: 160
        },
        {
          title: '创建人',
          field: 'add_user_name',
          width: 100
        },
        {
          title: '审核时间',
          field: 'auditor_time',
          width: 160
        },
        {
          title: '审核人',
          field: 'auditor_name',
          width: 100
        }
      ]
    }
  }
}